</tr>
<tr>
<td>mapId</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>The Mapbox Map ID.</td>
</tr>
<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>The public access token for the imagery.</td>
</tr>
<tr>
<td>format</td>
<td>String</td>
<td><code v-pre="">'png'</code></td>
<td><code v-pre="">optional</code>The format of the image request.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The ellipsoid. If not specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td>0</td>
<td><code v-pre="">optional</code>The minimum level-of-detail supported by the imagery provider. Take care when specifying this that the number of tiles at the minimum level is small, such as four or less. A larger number is likely to result in rendering problems.</td>
